import { Component } from '@angular/core';
import { NavController } from '@ionic/angular';
import { Platform } from '@ionic/angular';
import { FingerprintAIO ,FingerprintOptions} from '@ionic-native/fingerprint-aio/ngx';
import {BarcodeScanner, BarcodeScannerOptions } from '@ionic-native/barcode-scanner/ngx';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  fingerprintOptions : FingerprintOptions;
  constructor(private fingerprint: FingerprintAIO,private platform: Platform, private barcode : BarcodeScanner, public navCtrl : NavController) {
    this.fingerprintOptions={
      clientId: 'fingerprint-demo',
      clientSecret: 'password',
      disableBackup: true
    }
    this.scanBarcode = this.scanBarcode.bind(this);
    this.showFingerprintAuthDlg = this.showFingerprintAuthDlg.bind(this);
      this.receiveMessage = this.receiveMessage.bind(this);
      window.addEventListener("message", this.receiveMessage, false);
  }
  async showFingerprintAuthDlg(message){
    try{
      await this.platform.ready();
      const available = await this.fingerprint.isAvailable();
      console.log(available)
      if(available === "finger"){
        const result = await this.fingerprint.show(this.fingerprintOptions);
        console.log(result);
          window.frames[0].postMessage({
            event: "FingerPrintAccepted",
            params: message.data.params
          }, "*");

      }
    }
    catch(e)
    {
      console.error(e);
    }

  }
  options: BarcodeScannerOptions;


async scanBarcode(){
  const result = await this.barcode.scan();
    console.log(result)

      window.frames[0].postMessage({
        event: "BarcodeScanned",
        params: {did: result.text}
      },"*");

}

receiveMessage(message) {
  //alert(message.data);
  console.log(message.data.params)
  console.log(message.data.event)
//  this.scanBarcode();
  if(message.data.event.toLowerCase() === "InvokeBarCode".toLowerCase()){
    console.log("Inside If_else")
     this.scanBarcode();
  }else if(message.data.event.toLowerCase() == "InvokeFingerPrint".toLowerCase()){
    console.log("In side auth fingerprint")
    this.showFingerprintAuthDlg(message);
  //alert(message.data);
}

}

}
